import {getStorage} from '../localStorage';

let serialized=getStorage();

if(typeof serialized=="undefined"){
    serialized=false;
}
const initialState={
    homes:[],
    num_cards:3,
    loading:true,
    error:false,
    account:serialized,
    in_system:false,
}
console.log(initialState);
const reducer=(state=initialState , action)=>{
    switch (action.type) {
        case 'HOMES_READY':{
            return {
                ...state,
                loading:false,
                homes:action.payload,
            }
        };
        case 'LOADING':{
            return {
                ...state,
                loading:action.payload
            }
        };
        case 'ERROR':{
            return {
                ...state,
                error:action.payload
            }
        };
        case 'SUBMIT_REGISTER':{
            return {
                ...state,
                account:action.payload,
                in_system:true,
            }
        };
        case 'SUBMIT_LOGIN':{
            const user_account=action.payload;
            const state_account=state.account;

            if(user_account.email==state_account.email && user_account.password==state_account.password){
                return {
                    ...state,
                    in_system:true,
                } 
            }
            else{
                return {
                    ...state,
           
                }
            }

        };
        default: return state;

    }
}

export {
    reducer
}