
const saveStorage=(state)=>{

    try{
        let serialized=JSON.stringify(state);
        localStorage.setItem('state', serialized);
    }
    catch(err){
        // some 
    }

}

const getStorage=()=>{
    try{
        let serialized=localStorage.getItem('state');
        if(serialized==='null'){
            return undefined;
        }
        return JSON.parse(serialized);
    }
    catch(err){
        console.log('err');
        return undefined;
    }
}

export {
    saveStorage,
    getStorage
}