const homeLoaded=(newHomes)=>{
    return {
        type: 'HOMES_READY',
        payload: newHomes
    }
}

const addLoading=(bool)=>{
    return{
        type: 'LOADING',
        payload:bool,
    }
}
const addError=(bool)=>{
    return{
        type: 'ERROR',
        payload:bool,
    }
}
const submitRegister=(account)=>{
    return{
        type: 'SUBMIT_REGISTER',
        payload:account,
    }
}
const submitLogin=(account)=>{
    return{
        type: 'SUBMIT_LOGIN',
        payload:account,
    }
}



export {
    homeLoaded,
    addLoading,
    addError,
    submitRegister,
    submitLogin

}