import React from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import './header.css';

class Header extends React.Component{

    componentDidUpdate(){
        if(this.props.in_system==true){
            console.log(this.props.in_system);
        }
    }

    render(){

        let {in_system}=this.props;
  
        let content=(
            <React.Fragment>
                <Link className="number text-primary " to="/register">
                    Зарегистрироваться
                </Link>

                <button className="btn_call border-0 btn-success">
                    <Link to="/login" className="text-white text-decoration-none">
                        Войти
                    </Link>
                </button>
            </React.Fragment>
        );
        if(in_system){
            content="";
        }
        return(
            <header className="header" id="header">
                <div className="container">
                <nav className="navbar navbar-expand-lg navbar-light">
                <Link className="navbar-brand" to="/">
                    <img className="logo" src="/img/logo.jpg" alt="Logo"/>
                </Link>

                    <div className="navbar-content" id="navbarSupportedContent">
                        <ul className="navbar-nav">
                            <li className="nav-item active">
                                <Link className="nav-link" to="/sale/1">
                                Продажа
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/rent/1">
                                Аренда
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/village/1">
                                Поселки
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/about_us">
                                О компании
                                </Link>
                            </li>
                        </ul>
                        <div className="my-2 my-lg-0 d-flex align-items-center">
                            {content}
                        </div>
                    </div>
                    </nav>
                </div>
            </header>
        );
    }
}
const mapStateToProps=({in_system})=>{
    return{
        in_system
    }
}

export default connect(mapStateToProps)(Header);