import React from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {submitRegister} from '../../../actions';

import './register.css';


class Register extends React.Component{


    componentDidUpdate(prev){
        if(prev.in_system!==this.props.in_system){
            this.props.history.push('/sale/1');
        }
    }

    onSubmitForm=(e)=>{
        // e.preventDefault();
        let mas_input=['name', 'last_name', 'email', 'password','confirm_password'];
        
        
        let mas_value={};
        mas_input.forEach((item)=>{
            let val=document.getElementById(item).value;
            mas_value[[item]]=val;

        });

        let bool=Object.values(mas_value).every((item , index)=>{
            return item!=='';
        });


        if(bool==true){
            let {password, confirm_password}=mas_value;
            if(password==confirm_password){
               this.props.submitRegister(mas_value);
            }
        }
    }

    render(){
        return(
            <section className="register_section form_section">
                <div className="container">
                    
                    <div className="signup-form">
                        <form onSubmit={this.onSubmitForm} action="" method='post'>
                            <h2>Register</h2>
                            <p className="hint-text">Create your account. It's free and only takes a minute.</p>
                            <div className="form-group">
                                <div className="row">
                                    <div className="col-6"><input type="text" className="form-control" name="first_name" id="name" placeholder="First Name" required="required" /></div>
                                    <div className="col-6"><input type="text" className="form-control" name="last_name" id="last_name" placeholder="Last Name" required="required" /></div>
                                </div>        	
                            </div>
                            <div className="form-group">
                                <input type="email" id='email' className="form-control" name="email" placeholder="Email" required="required" />
                            </div>
                            <div className="form-group">
                                <input type="password" id="password" className="form-control" name="password" placeholder="Password" required="required" />
                            </div>
                            <div className="form-group">
                                <input type="password" id="confirm_password" className="form-control" name="confirm_password" placeholder="Confirm Password" required="required" />
                            </div>        
                            <div className="form-group">
                                <label className="checkbox-inline"><input type="checkbox" required="required" /> I accept the <a href="#">Terms of Use</a> &amp; <a href="#">Privacy Policy</a></label>
                            </div>
                            <div className="form-group">
                                <button type="submit" 
                                className="btn btn-success w-100"
                                onClick={this.onSubmitForm}
                                >Register Now</button>
                            </div>
                        </form>
                        <div className="text-center">Already have an account? <Link to="/login" className="text-primary">Sign in</Link></div>
                    </div>
                </div>
        </section>
        );
    }
}
const mapStateToProps=({account , in_system})=>{
    return{
        account, in_system
    }
}
const mapDispatchToProps=(dispatch)=>{
    return{
        submitRegister:(data)=>{
            dispatch(submitRegister(data));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Register);
