import React from 'react';
import './card.css';



const Card=({url, place, distance , id , dimensions_hundred , dimensions_meter , price})=>{
    let classNames='card';
    return(
        <div className="col-md-4 col-sm-6 col-12">
            <div className={classNames}>
                <img src={url} className="card-img-top" alt="..."/>
                <div className="card-body">
                    
                    <p className="card_text">Дом в поселке "{place}", {distance}, ID {id}</p>
                    <h4 className="card_price">{price}</h4>

                    <div className="card_block_options">
                        <span className="card_option map">
                            <i className="fa fa-map-o"></i>
                            <span>{dimensions_hundred}</span>
                        </span>

                        <span className="card_option home">
                            <i className="fa fa-home"></i>
                            <span>{dimensions_meter}</span>
                        </span>
                    </div>  
                </div>
            </div>
        </div>
    );
}

export default Card;