import React from 'react';
import {submitLogin} from '../../../actions';
import {connect} from 'react-redux';
import './login.css';

class Login extends React.Component{

    componentDidUpdate(prev){
        console.log(this.props.in_system);
        if(prev.in_system!==this.props.in_system){
            this.props.history.push('/sale/1');
        }
    }

    onSubmit=(e)=>{
        let email=document.getElementById('email');
        let password=document.getElementById('password');

        let email_val=email.value,
            password_val=password.value;
        
        if(email_val!=='' && password_val!==''){
            let login_data={
                email:email_val,
                password:password_val
            }
            this.props.submitLogin(login_data);
        }


    }
    render(){
        return(
        <section className="login_section ">
            <div className="container">
                <div className="card card-container">
                    <img id="profile-img" className="profile-img-card" src="//ssl.gstatic.com/accounts/ui/avatar_2x.png" />
                    <p id="profile-name" className="profile-name-card"></p>
                    <form className="form-signin" onSubmit={this.onSubmit}>
                        <span id="reauth-email" className="reauth-email"></span>
                        <input type="email" id="email" className="form-control" placeholder="Email address" required  />
                        <input type="password" id="password" className="form-control" placeholder="Password" required />
                        <div id="remember" className="checkbox">
                            <label>
                                <input type="checkbox" value="remember-me" /> Remember me
                            </label>
                        </div>
                        <button className="btn btn-lg btn-primary btn-block btn-signin" type="button" onClick={this.onSubmit}>Sign in</button>
                    </form>
                    <a href="#" className="forgot-password">
                        Forgot the password?
                    </a>
                </div>
            </div>
        </section>
        );
    }
}
const mapStateToProps=({in_system})=>{
    return{
        in_system
    }
}
const mapDispatchToProps=(dispatch)=>{
    return{
        submitLogin:(data)=>{
            dispatch(submitLogin(data));
        }
    }
}

export default connect(mapStateToProps , mapDispatchToProps)(Login);