import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './pagination.css';


class Pagination extends Component{

    state={
        prev:1,
        next:null,
        numbers:[]
    }


    componentWillMount(){
        this.updatePagination();
        


    }

    updatePagination(){
        let {data}=this.props;
        this.setState({...data});

    }
    componentDidUpdate(prev){
        if(prev.data!==this.props.data){
            this.updatePagination();
        }
    }



    render(){
        let {url, numbers, prev, next}=this.state;
        let prev_elem=(
        <li className="page-item">
            <Link className="page-link" to={`${prev}`} aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
                <span className="sr-only">Previous</span>
            </Link>
        </li>
        );
        if(!isFinite(prev)){
            prev_elem='';
        }
        let next_elem=(
            <li className="page-item">
                <Link className="page-link" to={`${next}`} aria-label="Next">
                    <span aria-hidden="true">&raquo;</span>
                    <span className="sr-only">Next</span>
                </Link>
            </li>
            );
        if(!isFinite(next)){
            next_elem='';
        }

        let numbers_elem=numbers.map(item=>{
            let to=`${item}`;
            return (
                <li className="page-item" key={item}>
                    <Link className="page-link" to={to}>{item}</Link>
                </li>
            )
        });


        return(
            <div className='row'>
                <div className="col-12 d-flex justify-content-center">
                    <nav aria-label="Page navigation example">
                        <ul className="pagination">
                            {prev_elem}
                            {numbers_elem}
                            {next_elem}
                        </ul>
                    </nav>
                </div>
            </div>
        );
    }
}

export default Pagination;