import React, {Fragment} from 'react';
import {connect} from 'react-redux';
import './about.css';
const About =({in_system})=>{

    let content=(
<Fragment>   
    <h1>О компании</h1>
    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Excepturi in vel illo molestias dolor fugiat, laboriosam temporibus architecto, nihil sint amet vitae molestiae, nisi quod doloremque consequuntur delectus suscipit modi! Cupiditate, aliquid facere eum perspiciatis accusantium vel pariatur recusandae dolorum iusto? Optio possimus magnam, quo at iste ab inventore sed laborum minus laboriosam accusamus excepturi, a eveniet, voluptate perferendis. Nobis dolorum veritatis ad voluptate voluptates, temporibus blanditiis suscipit quos minus hic praesentium nihil nemo optio delectus explicabo, at repellendus est quis enim autem ullam itaque officiis maxime quibusdam! Tenetur, harum, maiores! Corrupti animi voluptatibus dignissimos nulla, suscipit natus labore laborum, dolore deserunt aliquam. Nulla, incidunt, inventore? Magnam nesciunt, et magni praesentium? Aut ea, enim debitis quisquam recusandae quasi ab vitae nam cumque labore molestias dolorum commodi velit rem aliquam laboriosam quo at tempore dicta odit possimus magnam earum. Necessitatibus earum officia omnis velit ab reprehenderit fugit, repudiandae ad quis ipsam distinctio suscipit, tempore! Velit id aspernatur sint, reprehenderit a amet deleniti ipsam optio consequatur, quo obcaecati nam ratione. Delectus accusantium minus quos minima repellat natus nemo mollitia consequuntur ipsam blanditiis, cupiditate eos, illum sequi architecto, veniam et exercitationem. Architecto consequuntur blanditiis provident reiciendis atque voluptate, repudiandae enim ipsa ducimus ipsam.
    </p>
</Fragment>
    );

    if(!in_system){
        content=(
            <h1>Эта информация доступна только для авторизованных пользователей</h1>
        )
    }

    return(
        <section className="about" id='about'>
            <div className="container">
                {content}
            </div>
        </section>
    );
}

const mapStateToProps=({in_system})=>{
    return{
        in_system
    }
}
export default connect(mapStateToProps)(About);