import React, {Component} from 'react';
import {connect} from 'react-redux';
import {withDataPhone} from '../hoc';
import {homeLoaded, addLoading, addError} from '../../actions';
import Card from '../card';
import Pagination from '../pagination';
import ErrorBountry from '../errorBoundary';
import Error from '../error';
import Loader from '../loader';
import './homes.css';

const getNumericCards=(id, num_cards)=>{
        let before_num=(id-1)*num_cards;
        let after_num=before_num+num_cards;
        let arr=[];
        for(let i=before_num;i<after_num;i++){
            arr.push(i);
        }
        return arr;
}

class Homes extends Component{

    componentWillMount(){
        this.props.homeLoaded();
    }
    componentDidUpdate(prev){
        let {changeError}=this.props;
        if(prev.homes!==this.props.homes){
            if(this.props.homes.length===0){
                changeError(true);
            }
            else{
                changeError(false);
            }
        }
        else if(prev.id!==this.props.id){
            this.props.homeLoaded();
            changeError(false);
        }
    }
    getListObjects(){
        let arr_cards=this.props.homes;
   
            return arr_cards.map((item)=>{
                return <Card {...item} key={item.id} />
            });
    }

    getPaginationOptions(){
        let {id , method_pagination , num_cards}=this.props;
        let length=method_pagination();
        let pages=Math.ceil(length/num_cards);
        
        id=parseInt(id);
        let prev=(id-1)<1 ? 1 : (id-1);
        let next= (id+1)>pages ? pages : (id+1);

        let numbers=[];
        for(let i=1;i<=pages;i++){
            numbers.push(i);
        }

        return {
            prev,
            next,
            numbers,
        }


    }
    render(){
        let {loading , error}=this.props;
        let data, pagination;
        if(error){
            data=(<Error/>);
            pagination="";
        }
        else{
            if(loading){
                data=(<Loader/>);
                pagination="";
            }
            else{
                data=this.getListObjects();
                pagination=(<Pagination data={this.getPaginationOptions()}/>);
            }
        }

        
        return(
            <ErrorBountry>
                <section className="homes" id="homes">
                    <div className="container">
                        <div className="row">
                            {data}
                        </div>
                        {pagination}
                    </div>
                </section>
            </ErrorBountry>
        );
    }
}
const mapStateToProps=({homes , num_cards, loading, error})=>{
    return {
        homes, num_cards, loading, error
    }
}
const mapDispatchToProps=(dispatch , ownProperty)=>{
    let {method_search , id, num_cards}=ownProperty;
    return {
        homeLoaded :()=>{
            dispatch(addLoading(true));
            method_search(getNumericCards(id , num_cards))
            .then(data=>{
                dispatch(homeLoaded(data));
            })
            .catch(()=>{
                console.log('Ошибка');
            })
        },
        changeError:(bool)=>{
            dispatch(addError(bool));
        }
    }
}

export default  withDataPhone()(connect(mapStateToProps , mapDispatchToProps )(Homes));