    getObjects=(arr, quantity)=>{

        let data_test=[];
        quantity.forEach(element => {
            if(typeof arr[element]!=='undefined'){
                data_test.push(arr[element]);
            } 
        });

        
        return new Promise((resolve , reject)=>{
            setTimeout(()=>{
                resolve(data_test);
                // reject('error');
            } , 1000);
        });
    }
    getSaleLength=()=>{
        return this.homes.sale.length;
    }
    getRentLength=()=>{
        return this.homes.rent.length;
    }
    getVillageLength=()=>{
        return this.homes.village.length;
    }

    getSaleHomes=(quantity)=>{
        return this.getObjects(this.homes.sale, quantity);
    }
    getRentHomes=(quantity)=>{
        return this.getObjects(this.homes.rent, quantity);
    }
    getVillageHomes=(quantity)=>{
        return this.getObjects(this.homes.village, quantity);
    }
    getHome=(id)=>{
        let obj={};
        for(let value in this.homes){
            let bool=value.some((item)=>{
                if(item.id==id){
                    obj=item;
                }   
            });
            if(bool)
                return;
        }
        return new Promise((resolve , reject)=>{
            setTimeout(()=>{
               resolve(obj);
            }, 300);
        });

    }

    transformHome=(item)=>{
        let {url, place, distance, id, dimensions_hundred, dimensions_meter, price}=item;
        return {
            url,
            place,
            distance,
            id,
            dimensions_hundred,
            dimensions_meter,
            price,
        }
    }
