import React from 'react';
import {HomeConsumer} from '../homes-context';

const withDataPhone=()=>(Wrapped)=>{
    return (props)=>{
        return(
            <HomeConsumer>
                {(service)=>{
                    return(
                        <Wrapped {...props} service={service}/>
                    );
                }}
            </HomeConsumer>
        );
    }
}

export {
    withDataPhone
};