import React from 'react';
import {Route} from 'react-router-dom';
import {withDataPhone} from '../hoc';
import {connect} from 'react-redux';
import Header from '../header';
import Homes from '../homes';
import Footer from '../footer';
import About from '../pages/about';
import Login from '../pages/login';
import Register from '../pages/register';

const App =({service, num_cards})=>{


        return(
            <React.Fragment key='react-fragment-1'>
                <Header/>
                <Route path="/sale/:id" render={({match , history})=>{
                    let {id}=match.params;
                    console.log(id);
                    return <Homes id={id} method_pagination={service.getSaleLength} method_search={service.getSaleHomes} num_cards={num_cards}/>;
                }}/> 
                <Route path="/rent/:id" render={({match , history})=>{
                    let {id}=match.params;

                    return <Homes id={id} method_pagination={service.getRentLength} method_search={service.getRentHomes} num_cards={num_cards}/>;
                }}/> 
                <Route path="/village/:id" render={({match , history})=>{
                    let {id}=match.params;
                    return <Homes id={id} method_pagination={service.getVillageLength} method_search={service.getVillageHomes} num_cards={num_cards}/>;
                }}/> 
                <Route path="/about_us" component={About}/> 
                <Route path="/login" render={({match , history})=>{
                    return <Login history={history}/>
                }}/> 
                <Route path="/register" component={Register}/> 
                <Footer/>
            </React.Fragment>
            
        );
}
const maspStateToProps=({num_cards})=>{
    return{
        num_cards
    }
}
export default withDataPhone()(connect(maspStateToProps)(App));